/* Modern Professional Styles for Admin Dashboard */
/* Admin layout: sidebar + content */
.admin-layout {
  display: grid;
  grid-template-columns: 1fr;
  min-height: calc(100vh - 64px);
  background: #f8fafc;
}

/* Hide sidebar if present */
.admin-sidebar {
  display: none !important;
}

.admin-sidebar {
  position: relative;
  /* allow decorative border accent */
  top: auto;
  min-height: calc(100vh - 64px);
  height: auto;
  /* Match Navbar theme */
  background: linear-gradient(180deg, var(--uic-pink) 0%, var(--uic-dark-pink) 100%);
  color: #ffffff;
  /* Theme-aligned separator */
  border-right: 1px solid rgba(255, 255, 255, 0.18);
  padding: 24px 0;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 20px rgba(233, 30, 99, 0.25);
  overflow-y: visible;
  /* let it grow with page */
  z-index: 100;

  /* Subtle backdrop blur effect */
  backdrop-filter: blur(6px);

  /* Add subtle animation on load */
  animation: slideInLeft 0.25s ease-out;
}

/* Vertical accent that aligns with the theme and runs full height */
.admin-sidebar::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(180deg, var(--uic-dark-pink) 0%, var(--uic-pink) 100%);
  opacity: 0.35;
  pointer-events: none;
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.admin-sidebar h3 {
  font-size: 0.9rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  opacity: 0.9;
  margin: 0 24px 20px;
  font-weight: 700;
  color: #ffffff;
}

.admin-nav {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0 16px;
}

.admin-nav a,
.admin-nav button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border-radius: 10px;
  color: #ffffff;
  background: transparent;
  border: none;
  text-decoration: none;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 600;
  transition: all 0.2s ease;
  width: 100%;
  text-align: left;
  position: relative;
}

.admin-nav a:hover,
.admin-nav button:hover {
  background: rgba(255, 255, 255, 0.12);
  color: #FFFFFF;
  transform: translateX(4px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.admin-nav a.active,
.admin-nav button.active {
  background: rgba(255, 255, 255, 0.22);
  color: #ffffff;
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.15);
  transform: translateX(2px);
}

.admin-nav a:focus,
.admin-nav button:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

.admin-nav a span:first-child,
.admin-nav button span:first-child {
  font-size: 1rem;
  width: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.admin-nav a span:last-child,
.admin-nav button span:last-child {
  flex: 1;
}

/* Navigation separator */
.admin-nav-separator {
  height: 1px;
  background: rgba(148, 163, 184, 0.2);
  margin: 12px 16px;
  border-radius: 1px;
}

/* Better scrollbar styling */
.admin-sidebar::-webkit-scrollbar {
  width: 4px;
}

.admin-sidebar::-webkit-scrollbar-track {
  background: rgba(148, 163, 184, 0.1);
  border-radius: 2px;
}

.admin-sidebar::-webkit-scrollbar-thumb {
  background: rgba(59, 130, 246, 0.3);
  border-radius: 2px;
}

.admin-sidebar::-webkit-scrollbar-thumb:hover {
  background: rgba(59, 130, 246, 0.5);
}

.admin-content {
  padding: 24px 20px 40px;
}

.page-header { display: flex; align-items: center; justify-content: space-between; margin: 6px 0 12px; flex-wrap: wrap; gap: 10px; }
.breadcrumbs { display: inline-flex; align-items: center; gap: 8px; color: #64748b; font-weight: 600; }
.breadcrumbs .current { color: #0f172a; }
.breadcrumbs .sep { color: #cbd5e1; }
.kpis { display: inline-flex; gap: 8px; }
.kpi-chip { background: #ffffff; border: 1px solid #e5e7eb; border-radius: 10px; padding: 6px 10px; display: inline-flex; gap: 8px; align-items: baseline; box-shadow: 0 1px 2px rgba(16,24,40,.06); }
.kpi-chip .kpi-label { color: #64748b; font-weight: 600; font-size: 12px; }
.kpi-chip .kpi-value { color: #0f172a; font-weight: 800; }

.admin-topbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 16px;
}

.admin-topbar .title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #0f172a;
}

.admin-topbar .actions {
  display: flex;
  gap: 8px;
}

.admin-dashboard {
  padding: 0;
  background: transparent;
  min-height: unset;
  max-width: 100vw;
  overflow-x: hidden;
}

.admin-dashboard .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  width: 100%;
  box-sizing: border-box;
}

.dashboard-header {
  display: none;
}

.dashboard-header h1 {
  color: var(--uic-maroon);
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.dashboard-header p {
  color: var(--gray-600);
  font-size: 1.1rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
  width: 100%;
  max-width: 100%;
}

/* Increase specificity to override any legacy rules */
.admin-content .stats-grid {
  display: grid !important;
  grid-template-columns: repeat(5, minmax(0, 1fr)) !important;
}

/* 2 per row on medium screens */
@media (max-width: 1200px) {
  .stats-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

/* 1 per row on small screens */
@media (max-width: 640px) {
  .stats-grid {
    gap: 1rem;
    grid-template-columns: 1fr;
  }
}

.stat-card {
  background: white;
  border-radius: 14px;
  padding: 1.1rem;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  gap: 0.9rem;
  transition: all 0.25s ease;
  border: 1px solid var(--gray-200);
  border-left: 3px solid #e5e7eb;
  /* thin left accent */
  min-width: 0;
  /* allow grid item to shrink */
  max-width: 100%;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
}

.admin-dashboard .stat-card {
  border-left: none !important;
}

.admin-dashboard .stat-card::before {
  content: none !important;
}

.stat-icon {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  color: white;
  margin: 0;
}

.stat-card.users .stat-icon {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
}

.stat-card.pending .stat-icon {
  background: linear-gradient(135deg, #ef4444, #dc2626);
}

.stat-card.news .stat-icon {
  background: linear-gradient(135deg, #10b981, #059669);
}

.stat-card.jobs .stat-icon {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
}

.stat-card.tracer .stat-icon {
  background: linear-gradient(135deg, #ec4899, #db2777);
}

.stat-top { display: flex; align-items: baseline; gap: 8px; }

.stat-content h3 {
  color: var(--uic-pink);
  font-size: 1.6rem;
  font-weight: 800;
  margin: 0;
}

.trend-chip {
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.4;
}
.trend-chip.positive { background: #ecfdf5; color: #10b981; }
.trend-chip.negative { background: #fef2f2; color: #ef4444; }
.trend-chip.neutral { background: #f1f5f9; color: #64748b; }

.stat-content p {
  color: var(--gray-600);
  font-size: 0.95rem;
  margin: 0;
}

.stat-change {
  font-size: 0.8rem;
  font-weight: 600;
}

.stat-change.positive {
  color: var(--success);
}

.stat-change.negative {
  color: var(--danger);
}

.stat-change.neutral {
  color: var(--gray-500);
}

.dashboard-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin-bottom: 3rem;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
}

.charts-section {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid var(--gray-200);
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

/* Ensure each chart card stretches so bottoms align */
.chart-container {
  margin-bottom: 2rem;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

.chart-wrapper {
  margin-bottom: 2rem;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  /* fixed height provided inline (300px) to stabilize charts */
}

.chart-container canvas {
  max-width: 100% !important;
  height: auto !important;
}
.chart-container:last-child {
  margin-bottom: 0;
}

/* Chart grids */
.chart-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.chart-container.full-width {
  grid-column: 1 / -1;
}

.stat-card.users {
  border-left-color: #3b82f6;
  border-color: rgba(59, 130, 246, 0.25);
}

.stat-card.pending {
  border-left-color: #ef4444;
  border-color: rgba(239, 68, 68, 0.25);
}

.stat-card.news {
  border-left-color: #10b981;
  border-color: rgba(16, 185, 129, 0.25);
}

.stat-card.jobs {
  border-left-color: #8b5cf6;
  border-color: rgba(139, 92, 246, 0.25);
}

.stat-card.tracer {
