  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.25rem;
}
.chart-sub { color: #64748b; margin: 0 0 1rem 0; font-size: 0.9rem; }

.chart-range { display: inline-flex; gap: 6px; background: #f8fafc; padding: 4px; border-radius: 999px; border: 1px solid #e5e7eb; }
.chart-range .tab { border: none; background: transparent; padding: 6px 10px; border-radius: 999px; font-weight: 600; color: #64748b; cursor: pointer; }
.chart-range .tab.active { background: white; color: #0f172a; box-shadow: 0 1px 2px rgba(16,24,40,.06); }
.chart-range .tab:not(.active):hover { background: #eef2f7; }

.chart-actions {
  display: flex;
  gap: 0.5rem;
}

.chart-wrapper {
  position: relative;
}

.chart-edit-overlay {
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  height: 30px;
  pointer-events: none;
}

.year-edit-btn {
  position: absolute;
  bottom: 0;
  width: 24px;
  height: 24px;
  border: none;
  background: #8B0000;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  pointer-events: all;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  opacity: 0.8;
  transition: all 0.2s ease;
  transform: translateX(-50%);
}

.year-edit-btn:hover {
  opacity: 1;
  transform: translateX(-50%) scale(1.1);
  background: #660000;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
