}

.btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-primary {
  background: #8B0000;
  color: white;
}

.btn-primary:hover {
  background: #660000;
}

.btn-secondary {
  background: #6b7280;
  color: white;
}

.btn-secondary:hover {
  background: #4b5563;
}

.btn-outline {
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
}

.btn-outline:hover {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.btn-sm {
  padding: 0.375rem 0.75rem;
  font-size: 0.8rem;
}

/* Professional Chart Styling */
.professional-chart {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  background: white;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
  overflow: hidden;
}

.professional-chart::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #8B0000, #ff6b9d, #4ecdc4, #06d6a0);
}

.professional-chart h3 {
  color: #1f2937;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
}

.chart-wrapper-donut {
  position: relative;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chart-wrapper-donut canvas {
  max-height: 280px !important;
}

/* Enhanced chart containers */
.charts-row .chart-container {
