}

.chart-container h3 {
  color: var(--uic-maroon);
  margin-bottom: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.charts-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.activity-section {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid var(--gray-200);
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

.activity-section h3 {
