  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border: 1px solid var(--gray-200);
  border-radius: 10px;
  transition: all 0.3s ease;
}

.activity-item:hover {
  border-color: var(--uic-maroon);
  box-shadow: 0 4px 15px rgba(139, 0, 0, 0.1);
}

.activity-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--uic-light-gold);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--uic-maroon);
