.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.25rem;
  width: 100%;
  max-width: 100%;
}

/* Floating action cards */
.action-card {
  position: relative;
  display: grid;
  grid-template-columns: 56px 1fr;
  align-items: center;
  gap: 0.9rem;
  padding: 1rem 1.1rem;
  border: none;
  border-radius: 14px;
  background: linear-gradient(180deg, #ffffff 0%, #fbfbff 100%);
  box-shadow: 0 8px 28px rgba(16, 24, 40, 0.12), 0 1px 0 rgba(16, 24, 40, 0.04);
  color: #0f172a;
  font-weight: 600;
  transition: transform 200ms ease, box-shadow 200ms ease, background 200ms ease;
  cursor: pointer;
  outline: none;
  min-height: 96px;
}

/* Accent ring using pseudo-element */
.action-card::after {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 14px;
  padding: 2px; /* accent thickness */
  background: linear-gradient(90deg, #FFD34D 0%, #FFC107 35%, #FFE082 60%, #FFC107 100%);
  -webkit-mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0; /* hidden until hover/focus */
  transition: opacity 200ms ease;
  pointer-events: none;
}

.action-card:hover,
.action-card:focus-visible {
  transform: translateY(-4px);
  box-shadow: 0 16px 42px rgba(16, 24, 40, 0.18), 0 2px 0 rgba(16, 24, 40, 0.06);
}

.action-card:hover::after,
.action-card:focus-visible::after {
  opacity: 1;
}

.action-icon { width: 56px; height: 56px; border-radius: 12px; display: grid; place-items: center; color: white; }
.action-icon.users { background: linear-gradient(135deg, #3b82f6, #2563eb); }
.action-icon.news { background: linear-gradient(135deg, #10b981, #059669); }
.action-icon.jobs { background: linear-gradient(135deg, #8b5cf6, #7c3aed); }
.action-icon.tracer { background: linear-gradient(135deg, #ec4899, #db2777); }
.action-icon.analytics { background: linear-gradient(135deg, #14b8a6, #0ea5e9); }
.action-icon.gallery { background: linear-gradient(135deg, #f59e0b, #f97316); }
.action-icon.export { background: linear-gradient(135deg, #6366f1, #4338ca); }
.action-icon.invite { background: linear-gradient(135deg, #22c55e, #16a34a); }
.action-icon.link { background: linear-gradient(135deg, #06b6d4, #0891b2); }
.action-icon.email { background: linear-gradient(135deg, #ef4444, #dc2626); }
.action-icon.pending { background: linear-gradient(135deg, #f97316, #ea580c); }

.action-text { display: grid; gap: 4px; align-content: center; }
.action-title { font-weight: 800; color: #0f172a; }
.action-sub { font-weight: 600; color: #64748b; font-size: 12px; }

.action-controls { grid-column: 1 / -1; margin-top: 8px; }

/* Dense layout on small screens */
@media (max-width: 640px) {
  .actions-grid { grid-template-columns: repeat(auto-fit, minmax(180px, 1fr)); gap: 0.75rem; }
  .action-card { padding: 0.9rem; }
  .action-icon { width: 48px; height: 48px; }
}


/* Responsive Design */
@media (max-width: 1024px) {
  .admin-layout {
    grid-template-columns: 1fr;
  }

  .admin-sidebar {
    padding: 24px 8px;
    width: 80px;
  }

  .admin-sidebar h3 {
    display: none;
  }

  .admin-nav {
    padding: 0 8px;
  }

  .admin-nav a,
  .admin-nav button {
    justify-content: center;
    padding: 12px 8px;
    gap: 0;
  }

  .admin-nav a span:last-child,
  .admin-nav button span:last-child {
    display: none;
  }

  .admin-nav a span:first-child,
  .admin-nav button span:first-child {
    font-size: 1.125rem;
  }

  .dashboard-content {
    grid-template-columns: 1fr;
  }

  .charts-row {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .admin-layout {
    grid-template-columns: 1fr;
  }

  .admin-sidebar {
    padding: 16px 4px;
    width: 64px;
  }

  .admin-nav {
    padding: 0 4px;
  }

  .admin-nav a,
  .admin-nav button {
    padding: 10px 6px;
  }

  .admin-content {
    padding: 16px;
  }

  .dashboard-header h1 {
    font-size: 2rem;
  }

  .stats-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }

  .stat-card {
    padding: 1rem;
  }

  .stat-content h3 {
    font-size: 1.5rem;
  }

  .charts-section,
  .activity-section,
  .quick-actions {
    padding: 1.5rem;
  }

  .actions-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media (max-width: 480px) {
  .admin-layout {
    grid-template-columns: 1fr;
  }

  .admin-sidebar {
    padding: 12px 2px;
    width: 56px;
  }

  .admin-nav {
    padding: 0 2px;
  }

  .admin-nav a,
  .admin-nav button {
    padding: 8px 4px;
    border-radius: 6px;
  }

  .admin-nav a span:first-child,
  .admin-nav button span:first-child {
    font-size: 1rem;
  }

  .admin-content {
    padding: 12px;
  }

  .dashboard-header h1 {
    font-size: 1.8rem;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .stat-card {
    flex-direction: column;
    text-align: center;
  }

  .charts-section,
  .activity-section,
  .quick-actions {
    padding: 1rem;
  }

  .activity-item {
    flex-direction: column;
    text-align: center;
  }

  .actions-grid {
    grid-template-columns: 1fr;
  }
}